.modalToolbar {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4); 
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: 0.5s;
    pointer-events: none;
}

.active {
opacity: 1;
pointer-events: all;

}

.modalContent {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 30vw;
    transform: scale(0.5);
    transition: 0.4s all;
}

.modalContent.active {
    transform: scale(1);
}